<template>
	<div id="app">
		<headerpageleft></headerpageleft>
		<headernav></headernav>
		<el-row :gutter="20">
			<el-container style="width: 100%;padding-top: 3.125rem;padding-bottom: 3.125rem;">
				<el-col :span="15" :offset="3">
					<el-main v-bind:style="{minHeight: Height+'px'}" style="border: solid 1px gray;border-radius: 10px;">
						<component v-bind:is="privatecurrent"></component>
					</el-main>
				</el-col>
				<el-col :span="4">
					<blognews></blognews>
					<blogitems></blogitems>
				</el-col>
			</el-container>
			<el-footer style="text-align: center;">Copyright © 2020 Anwesende Powered by Vue</el-footer>
		</el-row>
	</div>
</template>

<script>
	import headernav from './components/headernav.vue'
	import mainpage from './components/blog/main.vue'
	import headerpageleft from './components/headerpage_left.vue'
	import rightaside from './components/Aside.vue'
	import blognews from './components/blognews.vue'
	import blogitems from './components/blogitems.vue'
	import headerpageright from './components/headerpage_right.vue'
	import cv from './components/Catalog/curriculum_vitae.vue'
	import blogmain from './components/Catalog/blogmain.vue'
	import github from './components/blog/github.vue'
	import csharpstring from './components/blog/csharpstring.vue'
	import test from './components/blog/test.vue'
	import elementstydu from './components/blog/element.vue'
	export default {
		name: 'app',
		components: {
			headernav,
			mainpage,
			rightaside,
			blognews,
			blogitems,
			headerpageright,
			headerpageleft,
			cv,
			blogmain,
			github,
			csharpstring,
			test,
			elementstydu
		},
		data() {
			return {
				Height: 0,
				Width: 0,
				privatecurrent: "test"
			}
		},
		methods: {
			autoRootFontSize() {
				document.documentElement.style.fontSize = Math.min(screen.width, document.documentElement.getBoundingClientRect().width) /
					720 * 16 + 'px';
				// 取screen.width和document.documentElement.getBoundingClientRect().width的最小值；除以750，乘以32；懂的起撒，就是原本是750大小的32px;如果屏幕大小变成了375px,那么字体就是16px;也就是根字体fontSize大小和屏幕大小成正比变化！是不是很简单
			}
		},
		mounted() {
			//动态设置内容高度 让footer始终居底   header+footer的高度是100
			this.Height = document.documentElement.clientHeight - 100;
			//监听浏览器窗口变化　
			window.onresize = () => {
				this.Height = document.documentElement.clientHeight - 100
				this.autoRootFontSize()
			}
		},

		computed: {
			changeCurrent() {
				return this.$store.state.current;
			}
		},
		watch: {
			changeCurrent() {
				this.privatecurrent = this.$store.state.current;
			}
		}
	}
</script>

<style>
	#app {
		position: absolute;
		top: 0;
		left: 0;
		height: 100%;
		width: 100%;
		padding: 0 10px;
		box-sizing: border-box;
	}
</style>
